.confirm {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 100000;
  top: 0;
  left: 0;
  background: rgba(69, 69, 69, 0.6);
  display: flex;
  // transition: transform 0.3s ease-in-out;
  // transform: translateY(var(--trans));
  transition: opacity 0.3s ease-in-out;
  opacity: var(--trans);
  .block {
    display: flex;
    margin: auto;
    // padding: calc(10vh + 1vw) calc(1vh + 10vw);
    width: 30%;
    height: 30%;
    border-radius: calc(1vh + 1vw);
    background: white;
    flex-direction: column;
    box-shadow: 2px 2px 0px 10px rgba(33, 33, 33, 0.7);
    transform: skewX(-0.2deg);
  }
  .body {
    flex: 1;
    font-size: calc(1.1vw + 1.1vh);
    font-weight: bolder;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 5%;
  }
  .foot {
    height: 20%;
    display: flex;
    flex-direction: row-reverse;
  }
  .yes, .no {
    height: 100%;
    width: calc(1vh + 5vw);
    white-space: nowrap;
    background: hotpink;
    border: 3px solid #333333;
    border-radius: 3px;
    transform: skewX(-15deg);
    box-shadow: 10px 10px 0px rgba(33, 33, 33, 0.8);
    color: white;
    font-size: 22px;
    font-weight: bolder;
    margin-right: 5%;
    margin-top: 3%;
    position: relative;
    padding: calc(0.3vh + 0.3vw);
    &::before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      white-space: nowrap;
      background: white;
      opacity: 0;
      height: 100%;
      width: calc(1vh + 5vw - 6px);
      font-size: 22px;
      font-weight: bolder;
      box-sizing: border-box;
      transition: opacity 0.1s linear;
    }
    &:hover::before {
      opacity: 0.4;
    }
  }
}