.slant {
  transform: skewX(-15deg);
  .content {
    transform: skewX(15deg);
  }
}
.frame {
  height: 100%;
  width: 100%;
  .body {
    width: 100%;
    height: 80%;
    display: flex;
    align-items: center;
  }
  .foot {
    overflow: hidden;
    height: 10%;
    width: 100%;
    position: relative;
  }
  .foot-blank {
    display: flex;
    height: 50%;
    width: 120%;
    position: absolute;
    bottom: 0;
    left: -10%;
    .left, .center, .right {
      flex: 1;
    }
    .left, .right {
      background: hotpink;
    }
    .center {
      background: white;
      display: flex;
      justify-content: center;
      align-items: center;
      .content {
        height: 100%;
        width: 100%;
        display: inline-block;
      }
    }
  }
}
.base-background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  filter: blur(2px);
}
.head {
  height: 10%;
  position: relative;
  display: flex;
  .background {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 0;
    transform: rotate(-5deg) translateX(-5%) translateY(-86%);
    height: 300%;
    width: 90%;
    background: yellow;
    box-shadow: 10px 11px 5px orange;
  }
  .left, .right {
    display: flex;
    height: 100%;
  }
  .left {
    flex: 1;
  }
  .right {
    padding-right: 8%;
  }
  .title-wrapper {
    height: calc(2vh + 2vw);
    position: relative;
    align-self: center;
    // margin-top: -1%;
    margin-left: 1%;
  }
  .title, .subtitle {
    font-weight: bold;
    position: absolute;
    bottom: 0;
    white-space: nowrap;
    color: #333333;
    &.hidden {
      position: static;
      color: yellow;
    }
  }
  .title {
    font-size: calc(1.5vh + 1.5vw);
    margin-left: 1%;
  }
  .subtitle {
    font-size: calc(1vh + 1vw);
    align-self: center;
  }
  .item {
    height: 60%;
    background: rgba(255, 107, 181, 0.95);
    color: white;
    font-size: calc(0.6vh + 0.6vw);
    padding: 0px 20px 0 20px;
    box-shadow: 8px 8px rgba(169, 169, 169, 0.45);
    align-self: flex-end;
    margin-right: 10px;
    position: relative;
    transform: skewX(-15deg);
    display: flex;
    align-items: center;
    .content {
      transform: skewX(15deg);
    }
    .plus {
      height: 16px;
      width: 16px;
      border: 3px solid white;
      border-radius: 50%;
      position: relative;
      margin-left: calc(0.3vh + 0.6vw);
      cursor: pointer;
      &::before {
        content: '+';
        display: block;
        text-align: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 16px;
        height: 16px;
        font-size: 16px;
        line-height: 16px;
        font-weight: bold;
      }
    }
    .tp-count {
      font-size: calc(0.8vh + 0.8vw);
      line-height: calc(0.8vh + 0.8vw);
    }
    .row {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      & > *:not(:nth-child(2)) {
        margin-left: 12px;
      }
    }
  }
}