.slant {
  transform: skewX(-15deg);
  .content {
    transform: skewX(15deg);
  }
}
.human {
  height: 100%;
  width: 100%;
  margin: auto;
  display: flex;
  align-items: center;
  position: relative;
  .jinsai-wrapper {
    height: 100%;
    width: 8.5%;
    margin-left: 1%;
    display: flex;
    align-items: center;
    position: relative;
  }
  .not-jinsai {
    height: 100%;
    // margin: auto;
    width: 85.5%;
    display: flex;
    align-items: center;
    position: relative;
    & > * {
      animation: appear 0.3s ease 1 forwards;
    }
    @keyframes appear {
      from {
        transform: scale(0);
      }
      to {
        transform: scale(1);
      }
    }
  }
  .left-button, .right-button {
    position: absolute;
    bottom: -10%;
    z-index: 1;
  }
  .left-button {
    left: 7%;
  }
  .right-button {
    right: 15%;
  }
  .foot-button {
    background: hotpink;
    color: white;
    padding: 10px;
    font-size: calc(1vh + 1vw);
    box-shadow: 10px 10px 0px rgba(33, 33, 33, 0.8);
    border: 5px solid rgba(33, 33, 33, 0.95);
    border-radius: 3px;
    margin-left: calc(2vh + 2vw);
    animation: button-appear 0.6s ease 1 forwards;
    @keyframes button-appear {
      from {
        opacity: 0;
        // filter: blur(5px);
      }
      to {
        opacity: 1;
        // filter: blur();
      }
    }
    &::before {
      content: attr(data-content);
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      white-space: nowrap;
      background: white;
      opacity: 0;
      padding: 10px;
      font-size: calc(1vh + 1vw);
      transition: opacity 0.1s linear;
    }
    &:hover::before {
      opacity: 0.4;
    }
  }
  .recident {
    width: calc((0.8vh + 0.8vw) * 12);
    height: 80%;
    background: white;
    box-shadow: 1px 1px 5px 6px rgba(33, 33, 33, 0.9);
    border-radius: 5px;
    margin: 0 1%;
  }
  .recident-title {
    font-size: calc(1.2vh + 1.2vw);
    font-weight: bolder;
    margin-top: 5%;
    margin-left: 5%;
    margin-bottom: 15%;
  }
  .require {
    margin-left: 10%;
    font-size: calc(0.8vh + 0.8vw);
    color: #333333;
    display: flex;
    align-items: center;
  }
  .left {
    flex: 1;
  }
  .circle {
    background: var(--color);
    height: calc(0.4vh + 0.4vw);
    width: calc(0.4vh + 0.4vw);
    border-radius: 50%;
    // margin-left: 1%;
    margin-right: 25%;
  }
  .shop {
    width: calc((0.8vh + 0.8vw) * 12);
    height: 80%;
    background: white;
    box-shadow: 1px 1px 5px 6px rgba(33, 33, 33, 0.9);
    border-radius: 5px;
    margin: 0 1%;
  }
  .good-row {
    margin-left: 7%;
    margin-bottom: 5%;
    margin-right: 7%;
    display: flex;
    align-items: center;
    cursor: pointer;
    box-shadow: 0 0 2px 1px #66ccff;
    border-radius: 5px;
    padding: 1% 5%;
  }
  .good {
    display: block;
    flex: 1;
    box-sizing: border-box;
  }
  .good-price {
    color: orange;
    &::after {
      content: 'p';
    }
  }
  .good-description {
    font-size: calc(0.5vh + 0.5vw);
    color: gray;
  }
  .good-name, .good-quantity {
    font-size: calc(0.6vh + 0.6vw);
  }
  .good-quantity {
    width: 20%;
    text-align: center;
    display: inline;
    &::before {
      content: '库存：';
      font-size: calc(0.5vw + 0.5vh);
      white-space: nowrap;
      color: gray;
    }
  }
  .good-name {
    padding: 1%;
    margin-right: 10px;
  }
  .buy {
    margin-right: 7%;
    background: orange;
    // border: 3px solid orangered;
    border: 0;
    border-radius: 5px;
    color: #f2f2f2;
    font-size: calc(0.7vw + 0.7vh);
  }
  .chocker {
    flex: 1;
    max-width: calc(56%);
  }
  .block {
    height: 80%;
    flex: 1;
    margin: 0 1%;
    .top {
      width: 100%;
      height: 45%;
      display: flex;
      justify-content: space-between;
    }
    .bottom {
      width: 100%;
      height: 55%;
    }
    .top > *, .bottom > * {
      box-sizing: border-box;
      overflow: hidden;
    }
  }
  .yanxiu {
    width: 48%;
    height: 90%;
    background: white;
    box-shadow: 1px 1px 5px 6px rgba(33, 33, 33, 0.9);
    border-radius: 5px;
  }
  .yanxiu-button-wrapper {
    width: 90%;
    height: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: auto;
    // border: 2px solid orange;
  }
  .yanxiu-button {
    width: 30%;
    height: 40%;
    border: 0;
    background: orange;
    border-radius: 5px;
    color: white;
    font-size: calc(1.0vw + 1.0vh);
  }
  .block-title {
    font-size: calc(1.2vh + 1.2vw);
    font-weight: bolder;
    margin-top: 5%;
    margin-left: 5%;
    margin-bottom: 5%;
  }
  .block-sub-title {
    color: gray;
    font-size: calc(0.8vh + 0.8vw);
    margin-top: -1%;
    margin-left: 6%;
  }
  .status-detail {
    width: 48%;
    height: 90%;
    background: white;
    box-shadow: 1px 1px 5px 6px rgba(33, 33, 33, 0.9);
    border-radius: 5px;
  }
  .skills-title {
    @extend .recident-title;
    margin: 3%;
  }
  .skills-detail {
    width: 100%;
    height: 100%;
    background: white;
    box-shadow: 1px 1px 5px 6px rgba(33, 33, 33, 0.9);
    border-radius: 5px;
  }
  .skills-detail-body {
    display: flex;
    width: 100%;
    height: calc(70% - 1.2vh - 1.2vw);
  }
  .skill-detail {
    width: 30%;
    height: 100%;
    margin-left: 2.5%;
    border-radius: 5px;
    background: hotpink;
    color: white;
    .skill-name {
      margin-left: 10%;
      margin-top: 3%;
    }
  }
  .skill-table {
    width: 100%;
    text-align: center;
  }
  .passive-skill {
    font-size: calc(0.5vh + 0.5vw);
    margin-top: 5%;
    text-align: center;
  }
}