.slant {
  transform: skewX(-15deg);
  .content {
    transform: skewX(15deg);
  }
}
.interview {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  .jinsai-wrapper {
    display: flex;
    align-items: center;
    position: relative;
    width: 8.5%;
    height: 100%;
    padding-left: 1%;
  }
  .interview-jinsai-init {
    transform: translateX(var(--init-trans));
    animation: push-left 0.3s ease-out var(--delay) forwards;
  }
  .background {
    background: url('./images/fan.jpg');
    height: 100vh;
    width: 100vw;
    position: absolute;
    z-index: -1;
    filter: blur(1.5px);
  }
  .foot-button {
    position: absolute;
    background: hotpink;
    color: white;
    padding: 10px;
    font-size: calc(1vh + 1vw);
    box-shadow: 10px 10px 0px rgba(33, 33, 33, 0.8);
    border: 5px solid rgba(33, 33, 33, 0.95);
    border-radius: 3px;
    bottom: -10%;
    z-index: 1;
    &::before {
      content: attr(data-content);
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      white-space: nowrap;
      background: white;
      opacity: 0;
      padding: 10px;
      font-size: calc(1vh + 1vw);
      padding: 10px;
      transition: opacity 0.1s linear;
    }
    &:hover::before {
      opacity: 0.4;
    }
    &.zhuchang {
      left: 15%;
    }
    &.xiangmu {
      right: 15%;
    }
  }
}