.property {
  // height: 12%;
  padding: 0 5%;
  margin-top: 10%;
  box-sizing: content-box;
  .row {
    display: flex;
  }
  .property-name {
    flex: 1;
  }
  .rank {
    color: var(--color);
    font-weight: bold;
    font-size: calc(0.7vh + 0.7vw);
    width: 22%;
    text-align: center;
    text-shadow:
      0 1px 1px black,
      1px 0 1px black,
      0 -1px 1px black,
      -1px 0 1px black;
    position: relative;
    &.rank-change {
      &::after {
        animation: bottom 1s 1s forwards;
      }
      &::before {
        animation: top 1s forwards;
      }
    }
    &::before, &::after {
      display: inline-block;
      position: absolute;
    }
    &::before {
      content: attr(data-rank);
      color: var(--color);
      top: 0%;
      opacity: 1;
    }
    &::after {
      content: attr(data-next-rank);
      color: var(--next-color);
      bottom: -50%;
      opacity: 0;
    }
    @keyframes top {
      from {
        top: 0%;
        opacity: 1;
      }
      to {
        top: -50%;
        opacity: 0;
      }
    }
    @keyframes bottom {
      from {
        bottom: -50%;
        opacity: 0;
      }
      to {
        bottom: 0%;
        opacity: 1;
      }
    }
  }
  .next-rank {
    @extend .rank;
    color: var(--next-color);
    opacity: 0;
    display: none;
    &.rank-change {
      display: block;
      opacity: 1;
    }
  }
  .progress {
    width: 90%;
    height: calc(0.6vh + 0.3vw);
    border: 3px solid #222222;
    border-radius: 2px;
    position: relative;
    &::before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: var(--percent);
      height: 100%;
      background: var(--color);
      // transition: width 1s;
      opacity: 0.77;
    }
    &.value-change::before {
      animation: value-change 2s forwards;
    }
  }
  @keyframes value-change {
    from {
      width: var(--percent);
      background: var(--color);
    }
    50% {
      width: var(--step1);
    }
    51% {
      width: var(--step2);
    }
    to {
      width: var(--next);
      background: var(--next-color);
    }
  }
}