html, body, #root {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
* {
  outline: 0;
  user-select: none;
  font-family: 'siyuan';
}
.clickable {
  cursor: pointer;
}
