.find-work {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  .background {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    background-image: url('./images/war.jpg');
    filter: blur(1px);
    background-position: center;
  }
  .work-count {
    background: hotpink;
    margin-left: 80%;
    margin-top: 2%;
    padding: calc(0.4vw + 0.4vh);
    max-width: 8%;
    border-radius: 8px;
    color: white;
    font-size: calc(1.4vw + 1.4vh);
    display: inline-block;
    justify-content: center;
    align-items: center;
    box-shadow: 8px 8px 0 rgba(69, 69, 69, 0.7);
  }
  .count-down {
    width: 20vw;
    height: 3%;
    position: relative;
    background: rgba(69, 69, 69, 0.7);
    border-radius: 8px;
    margin-left: 50%;
    &.counting::before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      width: 100%;
      height: 100%;
      border-radius: 4px;
      background: linear-gradient(to left, red, orange 40%, yellow 60%, lightgreen);
      background-size: 20vw 100%;
      animation: count 15s linear;
    }
  }
  @keyframes count {
    from {
      width: 100%;
    }
    to {
      width: 0%;
    }
  }
  .title {
    font-size: 30px;
    font-weight: bold;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5% 0;
    width: 65%;
    margin-top: 5%;
    span {
      display: block;
      padding: 5% 30%;
      border-radius: 8px;
      border: 3px solid lightgray;
      background:rgba(69, 69, 69, 0.7);
    }
  }
  .left-area {
    margin-left: 10%;
    width: 24%;
    height: 100%;
  }
  .button-area {
    width: 75%;
    text-align: center;
  }
  .jinsai-button {
    $button: calc(2vw + 2vh);
    background: url('./icons/button.svg') no-repeat;
    width: $button;
    height: $button;
    margin: auto 3%;
    font-size: calc(1vh + 1vw);
    border-radius: 50%;
    color: white;
    font-weight: bold;
    border: 0;
    cursor: pointer;
    position: relative;
    z-index: 11;
    &.next {
      transform: rotate(180deg);
    }
  }
  $jinsaiWidth: 8.5%;
  .jinsai-area {
    width: $jinsaiWidth * 11;
    display: flex;
    height: 80%;
    margin-top: -10%;
    margin-bottom: -30%;
    overflow: hidden;
    justify-content: center;
    align-items: center;
  }
  .jinsai-wrapper {
    width: 33%;
    height: 80%;
    padding-left: 1%;
    position: relative;
    transition: left 1s;
    &:nth-child(1) {
      z-index: 10;
      filter: drop-shadow(10px 10px 10px black);
    }
    &:nth-child(2) {
      left: -15%;
      top: 4px;
      z-index: 9;
      filter: brightness(75%);
    }
    &:nth-child(3) {
      left: -35%;
      top: 8px;
      z-index: 8;
      filter: brightness(50%);
    }
  }
}
