.slant {
  transform: skewX(-15deg);
  .content {
    transform: skewX(15deg);
  }
}
.azito {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  position: relative;
  .jinsai-wrapper {
    display: flex;
    align-items: center;
    position: relative;
    width: 8.5%;
    height: 100%;
    padding-left: 1%;
  }
  .azito-jinsai-init {
    transform: translateX(var(--init-trans));
    animation: push-left 0.3s ease-out var(--delay) forwards;
  }
  .azito-jinsai-disappearing {
    animation: azito-jinsai-disapear 0.3s forwards;
  }
  .azito-jinsai-pushing {
    animation: azito-jinsai-push 0.3s 0.3s forwards;
  }
  @keyframes push-left {
    from {
      transform: translateX(var(--init-trans));
    }
    to {
      transform: translateX(0);
    }
  }
  @keyframes azito-jinsai-push {
    from {
      transform: translateX(0);
    }
    to {
      transform: translateX(var(--trans));
    }
  }
  @keyframes azito-jinsai-disapear {
    from {

    }
    to {
      transform: scale(0);
      filter: blur(5px);
    }
  }
  .foot-button {
    position: absolute;
    background: hotpink;
    color: white;
    padding: 10px;
    font-size: calc(1vh + 1vw);
    box-shadow: 10px 10px 0px rgba(33, 33, 33, 0.8);
    border: 5px solid rgba(33, 33, 33, 0.95);
    border-radius: 3px;
    bottom: -10%;
    z-index: 1;
    animation: button-appear 0.6s ease 1 forwards;
    &::before {
      content: attr(data-content);
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      white-space: nowrap;
      background: white;
      opacity: 0;
      padding: 10px;
      font-size: calc(1vh + 1vw);
      transition: opacity 0.1s linear;
    }
    &:hover::before {
      opacity: 0.4;
    }
    &.zhuchang {
      left: 15%;
    }
    &.xiangmu {
      right: 15%;
    }
  }
}