.jinsai-block {
  height: 80%;
  width: 100%;
  background: hotpink;
  position: relative;
  box-shadow: 0 0 5px 5px;
  .card {
    position: absolute;
    bottom: -5%;
    right: -5%;
    width: 80%;
    height: 60%;
    min-height: calc(13.2vw + 13.3vh);
    background: whitesmoke;
    box-shadow: 0 0 5px 5px;
  }
  .name {
    color: white;
    font-weight: bolder;
    font-size: calc(0.8vh + 0.8vw);
    margin-top: -22%;
    text-shadow:
      0 1px 1px black,
      1px 0 1px black,
      0 -1px 1px black,
      -1px 0 1px black;
  }
  .status {
    margin-top: 5%;
    width: 100%;
    display: flex;
    justify-content: space-around;
  }
  .status-wrapper {
    width: calc(1.5vw + 1.5vh);
    height: calc(1.5vw + 1.5vh);
    border-radius: 50%;
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .skill {
    --height: calc(1.0vh + 1.0vw);
    margin: 10% auto 0;
    width: 80%;
    height: var(--height);
    font-size: calc(0.6vh + 0.6vw);
    background: hotpink;
    color: white;
    font-weight: bolder;
    border-radius: calc(var(--height) / 2);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  @keyframes shake {
    0% {}
    50% {
      transform: rotateY(-25deg);
    }
    100% {}
  }
}